import React from "react"
import { graphql } from "gatsby"
import Img from "gatsby-image"

import Row from "react-bootstrap/Row"
import Col from "react-bootstrap/Col"

import Layout from "../components/layout"
import SEO from "../components/seo"

const imgStyle = {
  marginRight: "auto",
  marginLeft: "auto",
  display: "block",
}

const ChampionPage = ({ data }) => {
  const champion = data.championsJson;
  return (
    <Layout activeIndex='champions'>
      <SEO title={champion.name}/>
      <Row>
        <Col md={"auto"} xs={12}>
          <Img
            style={imgStyle}
            fixed={champion.bannerImg.childImageSharp.fixed}
            alt={champion.name}
          />
        </Col>
        <Col>
          <h1>{champion.name}</h1>
          <p>{champion.lore}</p>
        </Col>
      </Row>
    </Layout>
  )
}

export const queryResult = graphql`
  query($pathName: String!) {
    championsJson (fields: { pathName: { eq: $pathName } }) {
      name
      id
      passive {
        name
      }
      lore
      bannerImg {
        childImageSharp {
          fixed(width: 308, quality: 100) {
            ...GatsbyImageSharpFixed
          }
        }
      }
    }
  }
`

export default ChampionPage;